import React, { useEffect } from 'react';
import IntroSection from 'components/Mentoring/IntroSection';
import Page from 'components/Layout/Page';
import BenefitSection from 'components/Mentoring/BenefitSection';
import CurriculumSection from 'components/Mentoring/CurriculumSection';
import MentorSection from 'components/Mentoring/MentorSection';
import ApplySection from 'components/Mentoring/ApplySection';
import ReviewSection from 'components/Mentoring/ReviewSection';
import { parseQueryString } from '@lubycon/utils';
import SEO from 'components/Utils/SEO';
import { logger } from '@lubycon/logger';
import { useCountDown } from 'src/hooks/useCountDown';
import { MENTORING_EXPIRY } from 'src/constants';
import { navigate } from 'gatsby';

const mentoringIntroPageLogger = logger.getPageLogger('mentoring_project_intro_page');

const MentoringIntro = () => {
  const { expired } = useCountDown(MENTORING_EXPIRY);

  useEffect(() => {
    mentoringIntroPageLogger.view();
  }, []);

  useEffect(() => {
    const referrer = document.referrer;
    const qs = location.search ?? ''; // eslint-disable-line
    const parsedQS = parseQueryString(qs.replace('?', ''));
    const utmSource = parsedQS.utm_source;

    mentoringIntroPageLogger.impression('mentoring_project_intro_referrer', {
      referrer,
      utmSource: (Array.isArray(utmSource) ? utmSource.join(',') : utmSource) ?? 'unknown',
    });
  }, []);

  return (
    <Page pageName=" ">
      <SEO
        title=" "
        description="     ,      ."
      />
      <IntroSection />
      <BenefitSection />
      <CurriculumSection />
      <MentorSection />
      <ReviewSection />
      <ApplySection
        onClickApply={() => {
          mentoringIntroPageLogger.click('apply_button', {
            position: 'bottom_section',
          });

          const ___ = 'https://forms.gle/DjyTPgscQaJTnTyF9';
          const __ = '/mentoring/apply-forms/';
          if (expired) {
            window.open(___);
          } else {
            navigate(__);
          }
        }}
      />
    </Page>
  );
};

export default MentoringIntro;
